@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.Navbar {
  width: 100%;
  padding: 10px 0;
  background-color: var(--dark);
  z-index: 999;
}

.NavbarDiv {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.WhoMadeThis {
  display: flex;
  color: var(--mainColor);
}

.WhoMadeThis p {
  margin: 0 0.5rem;
  font-size: 0.7rem;
}

.searchDiv {
  width: 650px;
  height: 50px;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
}

.searchDiv input {
  width: 100%;
  height: 100%;
  padding: 1rem;
  outline: none;
  color: #555;
}

.searchDiv .SearchButton {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 20%;
  cursor: pointer;
  background-color: red;
  border: none;
  color: #efefef;
}

.myProfile {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--mainColor);
}

.myProfile p {
  margin: 0 0.5rem;
  font-size: 0.7rem;
}

.SubNavBar {
  width: 100%;
  color: var(--mainColor);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.SubNavBar p {
  margin: 2rem;
  color: var(--mainColor);
  text-decoration: none !important;
}
.active {
  color: red;
}

.FilterDivContent {
  margin-bottom: 3rem;
}
.FilterContainer .FilterImgDiv {
  width: 450px;
  height: 300px;
  background-position: center;
  background-size: cover;
}

.FilteContent {
  color: rgb(206, 206, 206);
  letter-spacing: 1.4px;
}

.ContentFlex {
  display: flex;
}

.ContentFlex p {
  margin-right: 1.5rem;
}

.FilterGameDiv {
  height: auto;
  padding: 40px;
  background-position: center;
  background-color: #000;
  background-size: cover;
}
.FilterContainer {
  max-width: 1400px;
  margin: auto;
}

.BannerSection {
  width: 100%;
  height: 100vh;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 1)), url(https://images2.alphacoders.com/709/709939.png);
  display: flex;
  justify-content: center;
  align-items: center;
}

.BannerSection video {
  position: absolute;
  min-width: 70%;
  min-height: 100%;
  top: 0;
  left: 0;
}

.BannerContent img {
  width: 500px;
  height: auto;
}

.cropImg {
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 38%, 0 100%, 72% 48%, 89% 13%, 0 49%, 73% 10%, 0 34%);
          clip-path: polygon(0 0, 100% 0, 100% 38%, 0 100%, 72% 48%, 89% 13%, 0 49%, 73% 10%, 0 34%);
}

.SideBar {
  width: 500px;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.active {
  -webkit-transform: translateX(-100px);
          transform: translateX(-100px);
  border-radius: 5px;
}

.SideBarCard {
  width: 400px;
  height: 220px;
  margin: 1rem 0;
  background-size: cover;
  background-position: center;
  transition: all 0.3s ease;
}

.SideBarCard:hover {
  -webkit-transform: translateX(-10px);
          transform: translateX(-10px);
}

@media (max-width: 1400px) {
  .SideBar {
    display: none;
  }
  .BannerContent img {
    width: 200px;
    height: auto;
  }
}

.card {
  width: 350px;
  height: auto;
  border-radius: 10px;
  overflow: hidden;
  background-color: rgb(63, 60, 60);
  border-radius: 20px;
  padding: 1rem;
  margin: 1rem;
}

.Inner-Card {
  width: 100%;
  height: 220px;
  border-radius: 20px;
  overflow: hidden;
}

.ImgDiv {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background-position: center;
  background-size: cover;
  transition: all 0.3s ease;
}

.Inner-Card:hover .ImgDiv {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.card .CardContent h2 {
  font-size: 1.2rem;
  color: var(--mainColor);
  font-weight: 500;
}

.card .CardContent p {
  font-size: 0.7rem;
  color: var(--lightSmook);
}

.JoinDiv {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.JoinDiv .JoinDivFirst {
  display: flex;
  justify-content: center;
  align-items: center;
}
.JoinDivFirst img {
  margin-right: 0.3rem;
}
.JoinDivFirst p {
  margin: 0 0.3rem;
}

.JoinButton {
  border: none;
  background: rgb(2, 0, 36);
  background: linear-gradient(90deg, rgba(2, 0, 36, 1) 0%, rgba(48, 48, 156, 1) 0%, rgba(0, 212, 255, 1) 100%);
  padding: 0.7rem 2rem;
  border-radius: 20px;
  color: var(--mainColor);
  cursor: pointer;
}

.CardComponent {
  width: 100%;
  height: auto;
  padding: 30px 0;
  background-position: center;
  background-size: cover;
  background-color: #000;
}

.Container {
  max-width: 1800px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.CardComponent h1 {
  color: var(--mainColor);
  font-size: 1.4rem;
  text-align: center;
}

.ShowGameDiv {
  width: 100%;
  height: auto;
  padding: 40px 0;
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 1)), url(https://wallpaperaccess.com/full/7446.jpg);
  background-position: center;
  background-size: cover;
}

.ContainerDiv {
  max-width: 1400px;
  margin: auto;
  position: relative;
}

.ContainerDiv .Content {
  display: flex;
}

.ContainerDiv .Content > div:nth-child(2) {
  margin: 0 2rem;
  color: #e6e6e6;
}

.backButton {
  position: absolute;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid var(--mainColor);
  top: 80px;
  right: 0;
}

.backButton i {
  color: var(--mainColor);
}

.ScreenShots {
  display: flex;
}

.ScreenShotsSort {
  width: 300px;
  height: 200px;
  margin: 0 1rem;
  background-position: center;
  background-size: cover;
}

.VedioSection video {
  min-width: 100%;
  min-height: 60vh;
}

body {
  margin: 0;
  font-family: "Poppins", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

:root {
  --dark: #000;
  --mainColor: #fff;
  --lightSmook: #b8b8b8;
}

a:-webkit-any-link {
  text-decoration: none !important;
}

